import { Routes, Route } from 'react-router-dom';
import { ErrorBoundary } from '@/components/ErrorBoundary';
import { AppLayout } from '@/components/layout/AppLayout';
import { ThemeProvider } from '@/contexts/ThemeContext';
import SignalRProvider, { useSignalRContext } from '@/contexts/SignalRContext';
import { Dashboard } from '@/pages/Dashboard';
import AddonsPage from '@/pages/AddonsPage';
import EnhancedAddonsPage from '@/pages/EnhancedAddonsPage';
import AdvancedSearchPage from '@/pages/AdvancedSearchPage';
import AnalyticsPage from '@/pages/AnalyticsPage';
import RealTimeDownloads from '@/pages/RealTimeDownloads';
import { SystemStatus } from '@/pages/SystemStatus';
import SignalRTest from '@/pages/SignalRTest';
import SimpleSignalRTest from '@/pages/SimpleSignalRTest';

function App() {
  return (
    <ThemeProvider>
      <SignalRProvider>
        <ErrorBoundary>
          <AppLayout>
            <Routes>
              <Route path="/" element={<Dashboard />} />
              <Route path="/addons" element={<AddonsPage />} />
              <Route path="/addons-enhanced" element={<EnhancedAddonsPage />} />
              <Route path="/advanced-search" element={<AdvancedSearchPage />} />
              <Route path="/analytics" element={<AnalyticsPage />} />
              <Route path="/downloads" element={<RealTimeDownloads />} />
              <Route path="/system-status" element={<SystemStatus />} />
              <Route path="/signalr-test" element={<SignalRTest />} />
              <Route path="/simple-signalr" element={<SimpleSignalRTest />} />
            </Routes>
          </AppLayout>
        </ErrorBoundary>
      </SignalRProvider>
    </ThemeProvider>
  );
}

export default App;