import { useState } from 'react';
import { X, Settings, Download } from 'lucide-react';

interface DownloadSettingsModalProps {
  isOpen: boolean;
  onClose: () => void;
  onStartDownload: (settings: DownloadSettings) => void;
  initialSettings?: DownloadSettings;
}

export interface DownloadSettings {
  count: number;
  maxConcurrency: number;
  compatibility: string;
  forceRedownload: boolean;
}

const defaultSettings: DownloadSettings = {
  count: 10,
  maxConcurrency: 3,
  compatibility: '',
  forceRedownload: false
};

export const DownloadSettingsModal = ({
  isOpen,
  onClose,
  onStartDownload,
  initialSettings = defaultSettings
}: DownloadSettingsModalProps) => {
  const [settings, setSettings] = useState<DownloadSettings>(initialSettings);

  if (!isOpen) return null;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onStartDownload(settings);
    onClose();
  };

  const compatibilityOptions = [
    { value: '', label: 'All Compatibility' },
    { value: '2020', label: 'MSFS 2020 Only' },
    { value: '2024', label: 'MSFS 2024 Only' },
    { value: '2020-2024', label: 'MSFS 2020/2024' }
  ];

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full mx-4">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <div className="flex items-center space-x-2">
            <Settings className="w-5 h-5 text-blue-600" />
            <h2 className="text-lg font-semibold text-gray-900">Download Settings</h2>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 p-1 rounded-md hover:bg-gray-100"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        {/* Content */}
        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {/* Number of Addons */}
          <div>
            <label htmlFor="count" className="block text-sm font-medium text-gray-700 mb-2">
              Number of Addons to Download
            </label>
            <input
              type="number"
              id="count"
              min="1"
              max="50"
              value={settings.count}
              onChange={(e) => setSettings({ ...settings, count: parseInt(e.target.value) || 1 })}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
            <p className="text-xs text-gray-500 mt-1">Maximum: 50 addons</p>
          </div>

          {/* Concurrency */}
          <div>
            <label htmlFor="concurrency" className="block text-sm font-medium text-gray-700 mb-2">
              Maximum Concurrent Downloads
            </label>
            <div className="space-y-2">
              <input
                type="range"
                id="concurrency"
                min="1"
                max="10"
                value={settings.maxConcurrency}
                onChange={(e) => setSettings({ ...settings, maxConcurrency: parseInt(e.target.value) })}
                className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
              />
              <div className="flex justify-between text-xs text-gray-500">
                <span>1</span>
                <span className="font-medium text-blue-600">{settings.maxConcurrency}</span>
                <span>10</span>
              </div>
            </div>
            <p className="text-xs text-gray-500 mt-1">
              Higher values may impact system performance
            </p>
          </div>

          {/* Compatibility Filter */}
          <div>
            <label htmlFor="compatibility" className="block text-sm font-medium text-gray-700 mb-2">
              Compatibility Filter
            </label>
            <select
              id="compatibility"
              value={settings.compatibility}
              onChange={(e) => setSettings({ ...settings, compatibility: e.target.value })}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              {compatibilityOptions.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>

          {/* Force Redownload */}
          <div className="flex items-center">
            <input
              type="checkbox"
              id="forceRedownload"
              checked={settings.forceRedownload}
              onChange={(e) => setSettings({ ...settings, forceRedownload: e.target.checked })}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
            <label htmlFor="forceRedownload" className="ml-2 block text-sm text-gray-700">
              Force re-download existing files
            </label>
          </div>
          <p className="text-xs text-gray-500">
            By default, existing files are skipped to save time and bandwidth
          </p>

          {/* Action Buttons */}
          <div className="flex items-center justify-end space-x-3 pt-4 border-t border-gray-200">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-4 py-2 text-sm font-medium text-white bg-blue-600 border border-transparent rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 flex items-center space-x-2"
            >
              <Download className="w-4 h-4" />
              <span>Start Download</span>
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};