import { FC, useEffect, useState } from 'react';
import { XMarkIcon, CalendarIcon, UserIcon, DocumentIcon, ArrowDownTrayIcon, FolderIcon, InformationCircleIcon } from '@heroicons/react/24/outline';
import { AddonSummary } from '../../types/api';

interface AddonDetailsModalProps {
  addon: AddonSummary;
  isOpen: boolean;
  onClose: () => void;
  onDownload: () => void;
  isDownloading?: boolean;
  isDownloaded?: boolean;
}

const AddonDetailsModal: FC<AddonDetailsModalProps> = ({
  addon,
  isOpen,
  onClose,
  onDownload,
  isDownloading = false,
  isDownloaded = false
}) => {
  const [isVisible, setIsVisible] = useState(false);

  // Handle ESC key and visibility
  useEffect(() => {
    const handleEsc = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEsc);
      document.body.style.overflow = 'hidden';
      // Small delay to trigger animation
      setTimeout(() => setIsVisible(true), 10);
    } else {
      setIsVisible(false);
      document.body.style.overflow = 'unset';
    }

    return () => {
      document.removeEventListener('keydown', handleEsc);
      document.body.style.overflow = 'unset';
    };
  }, [isOpen, onClose]);

  if (!isOpen) return null;

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const formatDate = (date: string): string => {
    return new Date(date).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  const getCompatibilityBadgeColor = (compatibility: string) => {
    switch (compatibility) {
      case 'MSFS 2024':
        return 'bg-blue-100 text-blue-800 border-blue-200';
      case 'MSFS 2020':
        return 'bg-orange-100 text-orange-800 border-orange-200';
      case 'MSFS 2020/2024':
        return 'bg-green-100 text-green-800 border-green-200';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  return (
    <>
      {/* Backdrop */}
      <div
        className={`fixed inset-0 bg-black z-40 transition-opacity duration-300 ${
          isVisible ? 'bg-opacity-50' : 'bg-opacity-0'
        }`}
        onClick={onClose}
      />
      
      {/* Modal Container */}
      <div className="fixed inset-0 z-50 flex items-center justify-center p-4 pointer-events-none">
        <div
          className={`relative w-full max-w-3xl max-h-[90vh] overflow-y-auto bg-white rounded-xl shadow-xl transform transition-all duration-300 pointer-events-auto ${
            isVisible ? 'opacity-100 scale-100' : 'opacity-0 scale-95'
          }`}
          onClick={(e) => e.stopPropagation()}
        >
            {/* Header */}
            <div className="relative">
              {/* Thumbnail or Placeholder */}
              <div className="h-64 bg-gradient-to-br from-blue-50 to-indigo-100 rounded-t-xl flex items-center justify-center relative overflow-hidden">
                {addon.thumbnailUrl ? (
                  <img
                    src={addon.thumbnailUrl}
                    alt={addon.name}
                    className="w-full h-full object-cover"
                  />
                ) : (
                  <div className="flex flex-col items-center justify-center text-blue-400">
                    <DocumentIcon className="h-20 w-20 mb-2" />
                    <span className="text-lg font-medium">No preview available</span>
                  </div>
                )}
                {/* Compatibility Badge */}
                <div className="absolute top-4 right-4">
                  <span className={`inline-flex items-center px-3 py-1.5 text-sm font-medium rounded-md border ${getCompatibilityBadgeColor(addon.compatibility)}`}>
                    {addon.compatibility}
                  </span>
                </div>
              </div>
              
              {/* Close button */}
              <button
                onClick={onClose}
                className="absolute top-4 left-4 bg-white bg-opacity-90 hover:bg-opacity-100 rounded-full p-2 shadow-md transition-all"
              >
                <XMarkIcon className="h-5 w-5 text-gray-700" />
              </button>
            </div>

            {/* Content */}
            <div className="p-6">
              {/* Title and Download Button */}
              <div className="flex items-start justify-between mb-4">
                <div className="flex-1">
                  <h2 className="text-2xl font-bold text-gray-900 mb-2">
                    {addon.name}
                  </h2>
                  {addon.author && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <UserIcon className="h-4 w-4" />
                      <span>by {addon.author}</span>
                    </div>
                  )}
                </div>
                <button
                  onClick={onDownload}
                  disabled={isDownloading || isDownloaded}
                  className={`flex items-center gap-2 font-medium py-2.5 px-5 rounded-lg transition-colors ${
                    isDownloaded
                      ? 'bg-green-600 text-white'
                      : isDownloading
                      ? 'bg-gray-400 text-white cursor-not-allowed'
                      : 'bg-blue-600 text-white hover:bg-blue-700'
                  }`}
                >
                  {isDownloading ? (
                    <>
                      <div className="animate-spin rounded-full h-5 w-5 border-2 border-white border-t-transparent"></div>
                      Downloading...
                    </>
                  ) : isDownloaded ? (
                    <>
                      <DocumentIcon className="h-5 w-5" />
                      Downloaded
                    </>
                  ) : (
                    <>
                      <ArrowDownTrayIcon className="h-5 w-5" />
                      Download
                    </>
                  )}
                </button>
              </div>

              {/* Description */}
              {addon.description && (
                <div className="mb-6">
                  <h3 className="text-lg font-semibold text-gray-900 mb-2">Description</h3>
                  <p className="text-gray-600 leading-relaxed">{addon.description}</p>
                </div>
              )}

              {/* Details Grid */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-6">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900 mb-3">File Information</h3>
                  <div className="space-y-2">
                    <div className="flex items-center gap-2 text-gray-600">
                      <DocumentIcon className="h-4 w-4 text-gray-400" />
                      <span className="font-medium">Size:</span>
                      <span>{addon.fileSize ? formatFileSize(addon.fileSize) : 'Unknown'}</span>
                    </div>
                    <div className="flex items-center gap-2 text-gray-600">
                      <CalendarIcon className="h-4 w-4 text-gray-400" />
                      <span className="font-medium">Added:</span>
                      <span>{formatDate(addon.dateAdded)}</span>
                    </div>
                    {addon.fileName && (
                      <div className="flex items-center gap-2 text-gray-600">
                        <FolderIcon className="h-4 w-4 text-gray-400" />
                        <span className="font-medium">File:</span>
                        <span className="text-sm font-mono">{addon.fileName}</span>
                      </div>
                    )}
                  </div>
                </div>

                <div>
                  <h3 className="text-lg font-semibold text-gray-900 mb-3">Additional Details</h3>
                  <div className="space-y-2">
                    <div className="flex items-center gap-2 text-gray-600">
                      <InformationCircleIcon className="h-4 w-4 text-gray-400" />
                      <span className="font-medium">Addon ID:</span>
                      <span className="font-mono text-sm">{addon.id}</span>
                    </div>
                  </div>
                </div>
              </div>

              {/* Categories */}
              {addon.categories && addon.categories.length > 0 && (
                <div>
                  <h3 className="text-lg font-semibold text-gray-900 mb-3">Categories</h3>
                  <div className="flex flex-wrap gap-2">
                    {addon.categories.map((category, index) => (
                      <span
                        key={index}
                        className="inline-block bg-blue-100 text-blue-800 text-sm px-3 py-1 rounded-full"
                      >
                        {category}
                      </span>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
    </>
  );
};

export default AddonDetailsModal;