import * as signalR from '@microsoft/signalr';
import { toast } from 'react-hot-toast';
import { DownloadStatusResponse, DownloadItem } from '@/types/api';

export type ConnectionState = 'Disconnected' | 'Connecting' | 'Connected' | 'Reconnecting';

export interface ScraperStatus {
  isActive: boolean;
  progress: string;
  lastScrapeTime?: string;
  nextScheduledScrape?: string;
}

export interface SignalREvents {
  onProgressUpdate: (progress: DownloadStatusResponse) => void;
  onSessionCompleted: (sessionId: string) => void;
  onSessionFailed: (sessionId: string, error: string) => void;
  onItemCompleted: (sessionId: string, item: DownloadItem) => void;
  onItemFailed: (sessionId: string, item: DownloadItem, error: string) => void;
  onStatsUpdate: (stats: any) => void;
  onConnectionStateChanged: (state: ConnectionState) => void;
  ScraperStatusUpdate: (status: ScraperStatus) => void;
  onBatchCommitNotification: (batchInfo: any) => void;
  onSystemMetricsUpdate: (metrics: any) => void;
  onNewAddonNotification: (addon: any) => void;
}

class SignalRService {
  private connection: signalR.HubConnection | null = null;
  private connectionState: ConnectionState = 'Disconnected';
  private eventHandlers: Partial<SignalREvents> = {};
  private reconnectTimer: number | null = null;
  private sessionGroups: Set<string> = new Set();
  private connectionPromise: Promise<void> | null = null;
  private isConnecting: boolean = false;

  constructor() {
    // Don't initialize connection in constructor
    // It will be initialized on first connect() call
  }

  private initializeConnection() {
    const baseUrl = import.meta.env.VITE_API_URL || 'http://localhost:5269';
    
    this.connection = new signalR.HubConnectionBuilder()
      .withUrl(`${baseUrl}/downloadProgressHub`, {
        withCredentials: true,
        skipNegotiation: false,
        transport: signalR.HttpTransportType.WebSockets |
                   signalR.HttpTransportType.ServerSentEvents |
                   signalR.HttpTransportType.LongPolling,
      })
      .withAutomaticReconnect({
        nextRetryDelayInMilliseconds: (retryContext) => {
          // Exponential backoff: 0, 2, 4, 8, 16, 30, 30, 30...
          if (retryContext.previousRetryCount === 0) return 0;
          if (retryContext.previousRetryCount === 1) return 2000;
          if (retryContext.previousRetryCount === 2) return 4000;
          if (retryContext.previousRetryCount === 3) return 8000;
          if (retryContext.previousRetryCount === 4) return 16000;
          return 30000;
        }
      })
      .configureLogging(signalR.LogLevel.Information)
      .build();

    this.setupConnectionHandlers();
    this.setupMessageHandlers();
  }

  private setupConnectionHandlers() {
    if (!this.connection) return;

    this.connection.onreconnecting((error) => {
      console.log('SignalR: Reconnecting...', error);
      this.setConnectionState('Reconnecting');
      // Don't show toast for reconnecting to avoid spam
      // But log the error for debugging
      if (error) {
        console.error('SignalR: Reconnection error:', error);
      }
    });

    this.connection.onreconnected((connectionId) => {
      console.log('SignalR: Reconnected with ID:', connectionId);
      this.setConnectionState('Connected');
      
      // Avoid showing toast on development to reduce noise
      if (import.meta.env.PROD) {
        toast.success('Reconnected to server', { id: 'signalr-reconnect' });
      }
      
      // Rejoin all session groups
      if (this.sessionGroups.size > 0) {
        console.log('SignalR: Rejoining session groups after reconnect:', [...this.sessionGroups]);
        this.sessionGroups.forEach(sessionId => {
          this.joinSessionGroup(sessionId);
        });
      }
    });

    this.connection.onclose((error) => {
      console.log('SignalR: Connection closed', error);
      this.setConnectionState('Disconnected');
      
      // Only show error if we were previously connected
      if (this.connectionState === 'Connected' || this.connectionState === 'Reconnecting') {
        // Avoid showing toast on development to reduce noise
        if (import.meta.env.PROD) {
          toast.error('Connection to server lost', { id: 'signalr-disconnect' });
        }
      }
      
      // Log the error if present
      if (error) {
        console.error('SignalR: Connection close error:', error);
      }
      
      // Clear any existing reconnect timer
      if (this.reconnectTimer) {
        window.clearTimeout(this.reconnectTimer);
      }
      
      // Schedule reconnect with backoff
      console.log('SignalR: Scheduling reconnect in 5 seconds after close');
      this.reconnectTimer = window.setTimeout(() => {
        this.reconnectTimer = null;
        console.log('SignalR: Attempting to reconnect after close');
        this.connect();
      }, 5000);
    });
  }

  private setupMessageHandlers() {
    if (!this.connection) return;

    // Progress update for a specific session
    this.connection.on('ProgressUpdate', (progress: DownloadStatusResponse) => {
      console.log('SignalR: Progress update received', progress);
      this.eventHandlers.onProgressUpdate?.(progress);
    });

    // Session completed
    this.connection.on('SessionCompleted', (sessionId: string) => {
      console.log('SignalR: Session completed', sessionId);
      toast.success(`Download session completed`);
      this.eventHandlers.onSessionCompleted?.(sessionId);
    });

    // Session failed
    this.connection.on('SessionFailed', (sessionId: string, error: string) => {
      console.log('SignalR: Session failed', sessionId, error);
      toast.error(`Download session failed: ${error}`);
      this.eventHandlers.onSessionFailed?.(sessionId, error);
    });

    // Individual item completed
    this.connection.on('ItemCompleted', (sessionId: string, item: DownloadItem) => {
      console.log('SignalR: Item completed', sessionId, item);
      toast.success(`Downloaded: ${item.name}`);
      this.eventHandlers.onItemCompleted?.(sessionId, item);
    });

    // Individual item failed
    this.connection.on('ItemFailed', (sessionId: string, item: DownloadItem, error: string) => {
      console.log('SignalR: Item failed', sessionId, item, error);
      toast.error(`Failed to download ${item.name}: ${error}`);
      this.eventHandlers.onItemFailed?.(sessionId, item, error);
    });

    // Global stats update
    this.connection.on('StatsUpdate', (stats: any) => {
      console.log('SignalR: Stats update received', stats);
      this.eventHandlers.onStatsUpdate?.(stats);
    });

    // Scraper status update
    this.connection.on('ScraperStatusUpdate', (status: ScraperStatus) => {
      console.log('SignalR: Scraper status update received', status);
      this.eventHandlers.ScraperStatusUpdate?.(status);
    });

    // Batch commit notification
    this.connection.on('BatchCommitNotification', (batchInfo: any) => {
      console.log('SignalR: Batch commit notification received', batchInfo);
      this.eventHandlers.onBatchCommitNotification?.(batchInfo);
    });

    // System metrics update
    this.connection.on('SystemMetricsUpdate', (metrics: any) => {
      console.log('SignalR: System metrics update received', metrics);
      this.eventHandlers.onSystemMetricsUpdate?.(metrics);
    });

    // New addon notification
    this.connection.on('NewAddonNotification', (addon: any) => {
      console.log('SignalR: New addon notification received', addon);
      this.eventHandlers.onNewAddonNotification?.(addon);
    });
  }

  private setConnectionState(state: ConnectionState) {
    this.connectionState = state;
    this.eventHandlers.onConnectionStateChanged?.(state);
  }

  async connect(): Promise<void> {
    // If already connected, return immediately
    if (this.connection?.state === signalR.HubConnectionState.Connected) {
      console.log('SignalR: Already connected');
      // Force a state update to ensure UI reflects the connected state
      this.setConnectionState('Connected');
      return;
    }

    // If currently connecting or reconnecting, return the existing promise
    if ((this.isConnecting || this.connectionState === 'Reconnecting') && this.connectionPromise) {
      console.log('SignalR: Connection already in progress');
      return this.connectionPromise;
    }

    // Set connecting flag
    this.isConnecting = true;

    // Initialize connection if not already done
    if (!this.connection) {
      console.log('SignalR: Initializing connection for the first time');
      this.initializeConnection();
    }

    // Clear any pending reconnect timers
    if (this.reconnectTimer) {
      console.log('SignalR: Clearing reconnect timer');
      window.clearTimeout(this.reconnectTimer);
      this.reconnectTimer = null;
    }

    // Set up a connection timeout
    const connectionTimeout = setTimeout(() => {
      // If we're still connecting after 10 seconds, force to connected state
      // This handles the case where the server doesn't respond but connection works
      if (this.connectionState === 'Connecting' &&
          this.connection?.state === signalR.HubConnectionState.Connected) {
        console.log('SignalR: Connection timeout but actually connected, forcing state update');
        this.setConnectionState('Connected');
      }
    }, 10000);

    // Create connection promise
    this.connectionPromise = (async () => {
      try {
        console.log('SignalR: Starting connection');
        this.setConnectionState('Connecting');
        await this.connection?.start();
        console.log('SignalR: Connected successfully');
        this.setConnectionState('Connected');
        
        // Rejoin any session groups
        if (this.sessionGroups.size > 0) {
          console.log('SignalR: Rejoining session groups:', [...this.sessionGroups]);
          this.sessionGroups.forEach(sessionId => {
            this.joinSessionGroup(sessionId);
          });
        }
      } catch (error) {
        console.error('SignalR: Connection failed', error);
        this.setConnectionState('Disconnected');
        
        // Retry connection after 5 seconds, but only if we're not already reconnecting
        if (!this.reconnectTimer) {
          console.log('SignalR: Scheduling reconnect in 5 seconds');
          this.reconnectTimer = window.setTimeout(() => {
            this.reconnectTimer = null;
            this.connect();
          }, 5000);
        }
      } finally {
        clearTimeout(connectionTimeout);
        this.isConnecting = false;
        this.connectionPromise = null;
      }
    })();

    return this.connectionPromise;
  }

  async disconnect(): Promise<void> {
    if (this.reconnectTimer) {
      window.clearTimeout(this.reconnectTimer);
      this.reconnectTimer = null;
    }

    if (this.connection?.state === signalR.HubConnectionState.Disconnected) {
      return;
    }

    try {
      await this.connection?.stop();
      this.setConnectionState('Disconnected');
      this.sessionGroups.clear();
    } catch (error) {
      console.error('SignalR: Error disconnecting', error);
    }
  }

  async joinSessionGroup(sessionId: string): Promise<void> {
    if (this.connection?.state !== signalR.HubConnectionState.Connected) {
      console.warn('SignalR: Cannot join group - not connected');
      return;
    }

    try {
      await this.connection.invoke('JoinSessionGroup', sessionId);
      this.sessionGroups.add(sessionId);
      console.log(`SignalR: Joined session group ${sessionId}`);
    } catch (error) {
      console.error(`SignalR: Failed to join session group ${sessionId}`, error);
      toast.error('Failed to join download session updates');
    }
  }

  async leaveSessionGroup(sessionId: string): Promise<void> {
    if (this.connection?.state !== signalR.HubConnectionState.Connected) {
      return;
    }

    try {
      await this.connection.invoke('LeaveSessionGroup', sessionId);
      this.sessionGroups.delete(sessionId);
      console.log(`SignalR: Left session group ${sessionId}`);
    } catch (error) {
      console.error(`SignalR: Failed to leave session group ${sessionId}`, error);
    }
  }

  on<K extends keyof SignalREvents>(event: K, handler: SignalREvents[K]): void {
    this.eventHandlers[event] = handler as any;
  }

  off<K extends keyof SignalREvents>(event: K): void {
    delete this.eventHandlers[event];
  }

  getConnectionState(): ConnectionState {
    return this.connectionState;
  }

  isConnected(): boolean {
    return this.connection?.state === signalR.HubConnectionState.Connected;
  }
}

// Create singleton instance
export const signalRService = new SignalRService();