import React, { useEffect, useState } from 'react';
import * as signalR from '@microsoft/signalr';

export default function SignalRTest() {
  const [connectionState, setConnectionState] = useState<string>('Not started');
  const [error, setError] = useState<string>('');
  const [logs, setLogs] = useState<string[]>([]);

  const addLog = (message: string) => {
    setLogs(prev => [...prev, `[${new Date().toISOString()}] ${message}`]);
  };

  const testConnection = async () => {
    try {
      addLog('Starting connection test...');
      setConnectionState('Connecting...');
      setError('');

      const connection = new signalR.HubConnectionBuilder()
        .withUrl('http://localhost:5269/downloadProgressHub', {
          // Test with different configurations
          withCredentials: true,
          skipNegotiation: false,
          transport: signalR.HttpTransportType.WebSockets | 
                     signalR.HttpTransportType.ServerSentEvents | 
                     signalR.HttpTransportType.LongPolling,
        })
        .configureLogging(signalR.LogLevel.Trace) // Maximum logging
        .build();

      // Add connection state change handlers
      connection.onclose((error) => {
        addLog(`Connection closed: ${error?.message || 'No error'}`);
        setConnectionState('Disconnected');
      });

      connection.onreconnecting((error) => {
        addLog(`Reconnecting: ${error?.message || 'No error'}`);
        setConnectionState('Reconnecting');
      });

      connection.onreconnected((connectionId) => {
        addLog(`Reconnected with ID: ${connectionId}`);
        setConnectionState('Connected');
      });

      // Start the connection
      await connection.start();
      addLog('Connection started successfully!');
      setConnectionState('Connected');

      // Test sending a message
      if (connection.state === signalR.HubConnectionState.Connected) {
        try {
          await connection.invoke('JoinSessionGroup', 'test-session');
          addLog('Successfully joined test session group');
        } catch (err: any) {
          addLog(`Error invoking hub method: ${err.message}`);
        }
      }

      // Keep connection reference for cleanup
      (window as any).__testConnection = connection;

    } catch (err: any) {
      const errorMessage = `Connection failed: ${err.message || err}`;
      addLog(errorMessage);
      setError(errorMessage);
      setConnectionState('Failed');
      console.error('SignalR connection error:', err);
    }
  };

  const stopConnection = async () => {
    const connection = (window as any).__testConnection;
    if (connection) {
      try {
        await connection.stop();
        addLog('Connection stopped');
        setConnectionState('Stopped');
        delete (window as any).__testConnection;
      } catch (err: any) {
        addLog(`Error stopping connection: ${err.message}`);
      }
    }
  };

  useEffect(() => {
    // Cleanup on unmount
    return () => {
      stopConnection();
    };
  }, []);

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">SignalR Connection Test</h1>
      
      <div className="mb-4 space-y-2">
        <div className="flex items-center space-x-2">
          <span className="font-medium">Connection State:</span>
          <span className={`px-2 py-1 rounded text-sm ${
            connectionState === 'Connected' ? 'bg-green-100 text-green-800' :
            connectionState === 'Failed' ? 'bg-red-100 text-red-800' :
            connectionState === 'Connecting...' ? 'bg-yellow-100 text-yellow-800' :
            'bg-gray-100 text-gray-800'
          }`}>
            {connectionState}
          </span>
        </div>
        
        {error && (
          <div className="p-3 bg-red-50 border border-red-200 rounded text-red-700">
            {error}
          </div>
        )}
      </div>

      <div className="flex space-x-4 mb-6">
        <button
          onClick={testConnection}
          className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
        >
          Test Connection
        </button>
        <button
          onClick={stopConnection}
          className="px-4 py-2 bg-gray-600 text-white rounded hover:bg-gray-700"
        >
          Stop Connection
        </button>
        <button
          onClick={() => setLogs([])}
          className="px-4 py-2 bg-gray-400 text-white rounded hover:bg-gray-500"
        >
          Clear Logs
        </button>
      </div>

      <div className="bg-gray-900 text-gray-100 p-4 rounded font-mono text-sm overflow-auto max-h-96">
        <div className="font-bold mb-2">Connection Logs:</div>
        {logs.length === 0 ? (
          <div className="text-gray-500">No logs yet. Click "Test Connection" to start.</div>
        ) : (
          logs.map((log, index) => (
            <div key={index} className="mb-1">{log}</div>
          ))
        )}
      </div>

      <div className="mt-6 p-4 bg-blue-50 rounded">
        <h2 className="font-bold mb-2">Debug Information:</h2>
        <ul className="space-y-1 text-sm">
          <li>API URL: http://localhost:5269</li>
          <li>Hub Endpoint: /downloadProgressHub</li>
          <li>SignalR Version: {(signalR as any).VERSION || 'Unknown'}</li>
          <li>Browser: {navigator.userAgent}</li>
        </ul>
      </div>
    </div>
  );
}