import React, { createContext, useContext, useEffect, useState } from 'react';
import { signalRService, ConnectionState, ScraperStatus } from '@/services/signalr/signalRService';

// Define the context value type
interface SignalRContextValue {
  connectionState: ConnectionState;
  isConnected: boolean;
  connect: () => Promise<void>;
  disconnect: () => Promise<void>;
  scraperStatus: ScraperStatus | null;
}

// Create context with undefined default value
const SignalRContext = createContext<SignalRContextValue | null>(null);

/**
 * Custom hook to use the SignalR context
 * This must be used within a SignalRProvider
 */
export function useSignalRContext() {
  const context = useContext(SignalRContext);
  
  if (context === null) {
    throw new Error('useSignalRContext must be used within a SignalRProvider');
  }
  
  return context;
}

/**
 * SignalR provider component to manage connection state
 * This wraps the application to provide SignalR functionality
 */
export default function SignalRProvider({ children }: { children: React.ReactNode }) {
  // Track connection state
  const [connectionState, setConnectionState] = useState<ConnectionState>('Disconnected');
  const [isConnected, setIsConnected] = useState(false);
  const [initialized, setInitialized] = useState(false);
  const [scraperStatus, setScraperStatus] = useState<ScraperStatus | null>(null);

  // Initialize connection only once
  useEffect(() => {
    if (initialized) return;
    setInitialized(true);

    console.log('Initializing SignalR context');
    
    // Handle connection state changes
    const handleStateChange = (state: ConnectionState) => {
      console.log('SignalR state changed in context:', state);
      setConnectionState(state);
      setIsConnected(state === 'Connected');
    };

    // Handle scraper status updates
    const handleScraperStatus = (status: ScraperStatus) => {
      console.log('Scraper status update:', status);
      setScraperStatus(status);
    };

    // Subscribe to connection state changes FIRST
    signalRService.on('onConnectionStateChanged', handleStateChange);
    signalRService.on('ScraperStatusUpdate', handleScraperStatus);

    // Get initial state immediately
    const currentState = signalRService.getConnectionState();
    const connected = signalRService.isConnected();
    console.log('SignalR initial state:', currentState, 'isConnected:', connected);
    setConnectionState(currentState);
    setIsConnected(connected);

    // Auto-connect on mount (only once)
    if (!connected) {
      console.log('Auto-connecting to SignalR');
      signalRService.connect().then(() => {
        // Double-check state after connection
        const newState = signalRService.getConnectionState();
        const newConnected = signalRService.isConnected();
        console.log('SignalR state after connect:', newState, 'isConnected:', newConnected);
        if (newState !== connectionState || newConnected !== isConnected) {
          setConnectionState(newState);
          setIsConnected(newConnected);
        }
      }).catch(error => {
        console.error('Failed to connect to SignalR:', error);
      });
    }

    // Cleanup function - do not disconnect, just remove event handlers
    return () => {
      signalRService.off('onConnectionStateChanged');
      signalRService.off('ScraperStatusUpdate');
    };
  }, [initialized]);

  // Connect function
  const connect = async () => {
    try {
      await signalRService.connect();
    } catch (error) {
      console.error('Failed to connect to SignalR:', error);
    }
  };

  // Disconnect function
  const disconnect = async () => {
    try {
      await signalRService.disconnect();
    } catch (error) {
      console.error('Failed to disconnect from SignalR:', error);
    }
  };

  // Create context value
  const contextValue: SignalRContextValue = {
    connectionState,
    isConnected,
    connect,
    disconnect,
    scraperStatus
  };

  return (
    <SignalRContext.Provider value={contextValue}>
      {children}
    </SignalRContext.Provider>
  );
}