import { useEffect, useState } from 'react';
import { DownloadSessionCard } from './DownloadSessionCard';
import { useDownloadProgress } from '@/hooks/useSignalR';
import { DownloadStatusResponse } from '@/types/api';
import { toast } from 'react-hot-toast';

interface RealTimeDownloadSessionCardProps {
  initialSession: DownloadStatusResponse;
  onPauseSession?: (sessionId: string) => void;
  onCancelSession?: (sessionId: string) => void;
  onResumeSession?: (sessionId: string) => void;
  onPauseItem?: (sessionId: string, itemId: string) => void;
  onResumeItem?: (sessionId: string, itemId: string) => void;
  onCancelItem?: (sessionId: string, itemId: string) => void;
  showControls?: boolean;
}

export const RealTimeDownloadSessionCard = ({
  initialSession,
  onPauseSession,
  onCancelSession,
  onResumeSession,
  onPauseItem,
  onResumeItem,
  onCancelItem,
  showControls = true
}: RealTimeDownloadSessionCardProps) => {
  const [session, setSession] = useState<DownloadStatusResponse>(initialSession);

  // Use the SignalR hook for real-time updates with enhanced error handling
  const { progress, isConnected, connectionState } = useDownloadProgress({
    sessionId: initialSession.sessionId,
    onProgressUpdate: (update) => {
      // Update local state with real-time progress
      setSession(update);
    },
    onSessionCompleted: (sessionId) => {
      console.log(`Session ${sessionId} completed`);
      toast.success(`Download session completed`);
    },
    onSessionFailed: (sessionId, error) => {
      console.error(`Session ${sessionId} failed:`, error);
      toast.error(`Download session failed: ${error}`);
    },
    onItemCompleted: (sessionId, item) => {
      console.log(`Item ${item.name} completed in session ${sessionId}`);
      toast.success(`Downloaded: ${item.name}`);
    },
    onItemFailed: (sessionId, item, error) => {
      console.error(`Item ${item.name} failed in session ${sessionId}:`, error);
      toast.error(`Failed to download ${item.name}: ${error}`);
    }
  });

  // Update session from real-time progress
  useEffect(() => {
    if (progress) {
      setSession(progress);
    }
  }, [progress]);

  // Remove the toast notification for disconnection to avoid spam
  // The connection status indicator already shows the state visually

  return (
    <div className="relative">
      {/* Connection Status Indicator - Only show for active sessions */}
      {showControls && session.status === 'Active' && (
        <div className="absolute top-2 right-2 z-10">
          <div className={`flex items-center space-x-1 text-xs ${isConnected ? 'text-green-600' : 'text-red-600'}`}>
            <div className={`w-2 h-2 rounded-full ${isConnected ? 'bg-green-500' : 'bg-red-500'} ${isConnected ? 'animate-pulse' : ''}`} />
            <span>{isConnected ? 'Live' : 'Offline'}</span>
          </div>
        </div>
      )}

      {/* Render the regular DownloadSessionCard with real-time data */}
      <DownloadSessionCard
        session={session}
        onPauseSession={onPauseSession}
        onCancelSession={onCancelSession}
        onResumeSession={onResumeSession}
        onPauseItem={onPauseItem}
        onResumeItem={onResumeItem}
        onCancelItem={onCancelItem}
        showControls={showControls}
      />
    </div>
  );
};