import { useEffect, useState } from 'react';
import * as signalR from '@microsoft/signalr';

export default function SimpleSignalRTest() {
  const [connectionStatus, setConnectionStatus] = useState('Disconnected');
  const [messages, setMessages] = useState<string[]>([]);
  const [connection, setConnection] = useState<signalR.HubConnection | null>(null);

  useEffect(() => {
    // Only create the connection once
    const newConnection = new signalR.HubConnectionBuilder()
      .withUrl('http://localhost:5269/downloadProgressHub')
      .withAutomaticReconnect()
      .build();

    setConnection(newConnection);

    // Connection status handlers
    newConnection.onreconnecting(() => {
      setConnectionStatus('Reconnecting...');
      addMessage('Reconnecting to server...');
    });

    newConnection.onreconnected(() => {
      setConnectionStatus('Connected');
      addMessage('Reconnected to server');
    });

    newConnection.onclose(() => {
      setConnectionStatus('Disconnected');
      addMessage('Connection closed');
    });

    // Start the connection
    const startConnection = async () => {
      try {
        await newConnection.start();
        setConnectionStatus('Connected');
        addMessage('Connected to server');
      } catch (err) {
        setConnectionStatus('Failed to connect');
        addMessage(`Connection failed: ${err}`);
        // Try again in 5 seconds
        setTimeout(startConnection, 5000);
      }
    };

    startConnection();

    // Clean up on unmount
    return () => {
      // In development with StrictMode, don't actually close the connection
      // This prevents the double-mount issue
      if (import.meta.env.PROD) {
        newConnection.stop();
      }
    };
  }, []); // Empty dependency array ensures this runs once

  const addMessage = (message: string) => {
    setMessages(prev => [...prev, `${new Date().toLocaleTimeString()}: ${message}`]);
  };

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Simple SignalR Test</h1>
      
      <div className={`inline-flex items-center px-3 py-2 rounded-md mb-4 ${
        connectionStatus === 'Connected' ? 'bg-green-50 text-green-700' : 'bg-red-50 text-red-700'
      }`}>
        <span className="text-sm font-medium">{connectionStatus}</span>
        {connectionStatus === 'Connected' && (
          <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse ml-2" />
        )}
      </div>
      
      <div className="mt-4">
        <h2 className="text-lg font-semibold mb-2">Connection Log</h2>
        <div className="bg-gray-100 p-4 rounded-md h-64 overflow-y-auto">
          {messages.map((msg, index) => (
            <div key={index} className="mb-1 font-mono text-sm">{msg}</div>
          ))}
        </div>
      </div>
    </div>
  );
}