import { useEffect, useState, useCallback, useRef } from 'react';
import { signalRService, ConnectionState } from '@/services/signalr/signalRService';
import { DownloadStatusResponse, DownloadStats, DownloadItem } from '@/types/api';

export interface UseSignalROptions {
  autoConnect?: boolean;
}

export interface UseSignalRReturn {
  connectionState: string;
  isConnected: boolean;
  connect: () => Promise<void>;
  disconnect: () => Promise<void>;
}

export function useSignalR(options: UseSignalROptions = {}): UseSignalRReturn {
  const { autoConnect = false } = options;
  const [connectionState, setConnectionState] = useState<ConnectionState>('Disconnected');
  const [isConnected, setIsConnected] = useState(false);
  const connectAttemptedRef = useRef(false);

  useEffect(() => {
    const handleStateChange = (state: ConnectionState) => {
      setConnectionState(state);
      setIsConnected(state === 'Connected');
    };

    // Subscribe to connection state changes
    signalRService.on('onConnectionStateChanged', handleStateChange);

    // Set initial state
    const currentState = signalRService.getConnectionState();
    setConnectionState(currentState);
    setIsConnected(signalRService.isConnected());

    // Auto-connect if requested (only once per component lifecycle)
    if (autoConnect && !connectAttemptedRef.current) {
      connectAttemptedRef.current = true;
      
      if (!signalRService.isConnected()) {
        console.log('useSignalR: Auto-connecting to SignalR');
        signalRService.connect().catch(err => {
          console.error('useSignalR: Auto-connect failed:', err);
        });
      } else {
        console.log('useSignalR: Already connected, skipping auto-connect');
      }
    }

    return () => {
      signalRService.off('onConnectionStateChanged');
    };
  }, []); // Remove autoConnect from dependencies to prevent reconnection

  const connect = useCallback(async () => {
    try {
      await signalRService.connect();
    } catch (error) {
      console.error('Failed to connect to SignalR:', error);
    }
  }, []);

  const disconnect = useCallback(async () => {
    try {
      await signalRService.disconnect();
    } catch (error) {
      console.error('Failed to disconnect from SignalR:', error);
    }
  }, []);

  return {
    connectionState,
    isConnected,
    connect,
    disconnect
  };
}

export interface UseDownloadSessionsReturn {
  activeSessions: DownloadStatusResponse[];
  sessionCount: number;
}

export function useDownloadSessions(): UseDownloadSessionsReturn {
  const [activeSessions, setActiveSessions] = useState<DownloadStatusResponse[]>([]);
  // Track connection state to handle reconnection
  const [connectionState, setConnectionState] = useState<ConnectionState>(
    signalRService.getConnectionState()
  );

  // Watch for connection state changes to refresh data after reconnection
  useEffect(() => {
    const handleConnectionStateChange = (state: ConnectionState) => {
      const previousState = connectionState;
      setConnectionState(state);
      
      // If we just reconnected, we might need to refresh data
      if (previousState !== 'Connected' && state === 'Connected') {
        console.log('useDownloadSessions: Reconnected, sessions may need refresh');
        // Sessions will be updated automatically via SignalR events
      }
    };
    
    signalRService.on('onConnectionStateChanged', handleConnectionStateChange);
    
    return () => {
      signalRService.off('onConnectionStateChanged');
    };
  }, [connectionState]);

  useEffect(() => {
    console.log('useDownloadSessions: Setting up event handlers');
    
    // Handle progress updates which contain full session info
    const handleProgressUpdate = (session: DownloadStatusResponse) => {
      console.log('useDownloadSessions: Progress update received', session);
      setActiveSessions(prev => {
        const index = prev.findIndex(s => s.sessionId === session.sessionId);
        if (index >= 0) {
          // Update existing session
          const updated = [...prev];
          updated[index] = session;
          return updated;
        }
        // Add new session
        return [...prev, session];
      });
    };

    // Handle session completion
    const handleSessionCompleted = (sessionId: string) => {
      console.log('useDownloadSessions: Session completed', sessionId);
      setActiveSessions(prev => prev.filter(s => s.sessionId !== sessionId));
    };

    // Handle session failure
    const handleSessionFailed = (sessionId: string, error: string) => {
      console.log('useDownloadSessions: Session failed', sessionId, error);
      setActiveSessions(prev => prev.filter(s => s.sessionId !== sessionId));
    };

    // Register event handlers
    signalRService.on('onProgressUpdate', handleProgressUpdate);
    signalRService.on('onSessionCompleted', handleSessionCompleted);
    signalRService.on('onSessionFailed', handleSessionFailed);

    return () => {
      console.log('useDownloadSessions: Cleaning up event handlers');
      signalRService.off('onProgressUpdate');
      signalRService.off('onSessionCompleted');
      signalRService.off('onSessionFailed');
    };
  }, []);

  return {
    activeSessions,
    sessionCount: activeSessions.length
  };
}

export interface UseGlobalStatsOptions {
  onStatsUpdate?: (stats: DownloadStats) => void;
}

export interface UseGlobalStatsReturn {
  stats: DownloadStats | null;
}

export function useGlobalStats(options: UseGlobalStatsOptions = {}): UseGlobalStatsReturn {
  const { onStatsUpdate } = options;
  const [stats, setStats] = useState<DownloadStats | null>(null);
  const callbackRef = useRef(onStatsUpdate);
  // Track connection state to handle reconnection
  const [connectionState, setConnectionState] = useState<ConnectionState>(
    signalRService.getConnectionState()
  );

  // Update callback ref
  useEffect(() => {
    callbackRef.current = onStatsUpdate;
  }, [onStatsUpdate]);

  // Watch for connection state changes to refresh data after reconnection
  useEffect(() => {
    const handleConnectionStateChange = (state: ConnectionState) => {
      const previousState = connectionState;
      setConnectionState(state);
      
      // If we just reconnected, stats will be automatically updated
      if (previousState !== 'Connected' && state === 'Connected') {
        console.log('useGlobalStats: Reconnected, stats will refresh automatically');
      }
    };
    
    signalRService.on('onConnectionStateChanged', handleConnectionStateChange);
    
    return () => {
      signalRService.off('onConnectionStateChanged');
    };
  }, [connectionState]);

  useEffect(() => {
    console.log('useGlobalStats: Setting up stats handler');
    
    const handleStatsUpdate = (newStats: DownloadStats) => {
      console.log('useGlobalStats: Received stats update', newStats);
      setStats(newStats);
      if (callbackRef.current) {
        callbackRef.current(newStats);
      }
    };

    // Register event handler
    signalRService.on('onStatsUpdate', handleStatsUpdate);

    return () => {
      console.log('useGlobalStats: Cleaning up stats handler');
      signalRService.off('onStatsUpdate');
    };
  }, []);

  return { stats };
}

export interface UseDownloadProgressOptions {
  sessionId?: string;
  onProgressUpdate?: (session: DownloadStatusResponse) => void;
  onSessionCompleted?: (sessionId: string) => void;
  onSessionFailed?: (sessionId: string, error: string) => void;
  onItemCompleted?: (sessionId: string, item: DownloadItem) => void;
  onItemFailed?: (sessionId: string, item: DownloadItem, error: string) => void;
}

export interface UseDownloadProgressReturn {
  downloadProgress: Map<string, number>;
  downloadSpeeds: Map<string, number>;
  sessionData: DownloadStatusResponse | null;
  progress: DownloadStatusResponse | null;
  isConnected: boolean;
  connectionState: ConnectionState;
}

export function useDownloadProgress(options: UseDownloadProgressOptions = {}): UseDownloadProgressReturn {
  const {
    sessionId,
    onProgressUpdate,
    onSessionCompleted,
    onSessionFailed,
    onItemCompleted,
    onItemFailed
  } = options;

  const [downloadProgress, setDownloadProgress] = useState<Map<string, number>>(new Map());
  const [downloadSpeeds, setDownloadSpeeds] = useState<Map<string, number>>(new Map());
  const [sessionData, setSessionData] = useState<DownloadStatusResponse | null>(null);
  const [progress, setProgress] = useState<DownloadStatusResponse | null>(null);
  const [isConnected, setIsConnected] = useState<boolean>(signalRService.isConnected());
  
  // Track connection state to handle reconnection
  const [connectionState, setConnectionState] = useState<ConnectionState>(
    signalRService.getConnectionState()
  );

  // Create refs for the callbacks to avoid dependency issues
  const onProgressUpdateRef = useRef(onProgressUpdate);
  const onSessionCompletedRef = useRef(onSessionCompleted);
  const onSessionFailedRef = useRef(onSessionFailed);
  const onItemCompletedRef = useRef(onItemCompleted);
  const onItemFailedRef = useRef(onItemFailed);

  // Update callback refs when they change
  useEffect(() => {
    onProgressUpdateRef.current = onProgressUpdate;
    onSessionCompletedRef.current = onSessionCompleted;
    onSessionFailedRef.current = onSessionFailed;
    onItemCompletedRef.current = onItemCompleted;
    onItemFailedRef.current = onItemFailed;
  }, [onProgressUpdate, onSessionCompleted, onSessionFailed, onItemCompleted, onItemFailed]);

  // Watch for connection state changes to handle rejoining session groups
  useEffect(() => {
    const handleConnectionStateChange = (state: ConnectionState) => {
      const previousState = connectionState;
      setConnectionState(state);
      setIsConnected(state === 'Connected');
      
      // If we just reconnected and we're tracking a specific session, rejoin the group
      if (previousState !== 'Connected' && state === 'Connected' && sessionId) {
        console.log(`useDownloadProgress: Reconnected, rejoining session group ${sessionId}`);
        signalRService.joinSessionGroup(sessionId).catch(err => {
          console.error(`useDownloadProgress: Failed to rejoin session group ${sessionId}:`, err);
        });
      }
    };
    
    signalRService.on('onConnectionStateChanged', handleConnectionStateChange);
    
    return () => {
      signalRService.off('onConnectionStateChanged');
    };
  }, [connectionState, sessionId]);

  useEffect(() => {
    console.log(`useDownloadProgress: Setting up for ${sessionId || 'all sessions'}`);
    
    const handleProgressUpdate = (session: DownloadStatusResponse) => {
      // Only update if it's for our session (or we're tracking all sessions)
      if (!sessionId || session.sessionId === sessionId) {
        console.log(`useDownloadProgress: Update for session ${session.sessionId}`,
          session.items.length > 0 ?
            `Items: ${session.items.map(i => `${i.id}: ${i.progress}%`).join(', ')}` :
            'No items');
        
        setSessionData(session);
        setProgress(session);
        
        // Update progress and speeds for each item
        const newProgress = new Map<string, number>();
        const newSpeeds = new Map<string, number>();
        
        session.items.forEach(item => {
          const key = `${session.sessionId}-${item.id}`;
          newProgress.set(key, item.progress);
          newSpeeds.set(key, item.speedBytesPerSecond || 0);
        });
        
        setDownloadProgress(newProgress);
        setDownloadSpeeds(newSpeeds);
        
        // Call progress update callback if provided
        if (onProgressUpdateRef.current) {
          onProgressUpdateRef.current(session);
        }
      }
    };
    
    // Session completed handler
    const handleSessionCompleted = (completedSessionId: string) => {
      if (!sessionId || sessionId === completedSessionId) {
        console.log(`useDownloadProgress: Session completed ${completedSessionId}`);
        if (onSessionCompletedRef.current) {
          onSessionCompletedRef.current(completedSessionId);
        }
      }
    };
    
    // Session failed handler
    const handleSessionFailed = (failedSessionId: string, error: string) => {
      if (!sessionId || sessionId === failedSessionId) {
        console.log(`useDownloadProgress: Session failed ${failedSessionId}: ${error}`);
        if (onSessionFailedRef.current) {
          onSessionFailedRef.current(failedSessionId, error);
        }
      }
    };
    
    // Item completed handler
    const handleItemCompleted = (itemSessionId: string, item: DownloadItem) => {
      if (!sessionId || sessionId === itemSessionId) {
        console.log(`useDownloadProgress: Item completed ${item.id} in session ${itemSessionId}`);
        if (onItemCompletedRef.current) {
          onItemCompletedRef.current(itemSessionId, item);
        }
      }
    };
    
    // Item failed handler
    const handleItemFailed = (itemSessionId: string, item: DownloadItem, error: string) => {
      if (!sessionId || sessionId === itemSessionId) {
        console.log(`useDownloadProgress: Item failed ${item.id} in session ${itemSessionId}: ${error}`);
        if (onItemFailedRef.current) {
          onItemFailedRef.current(itemSessionId, item, error);
        }
      }
    };

    // Join session group if sessionId is provided
    if (sessionId) {
      console.log(`useDownloadProgress: Joining session group ${sessionId}`);
      signalRService.joinSessionGroup(sessionId).catch(err => {
        console.error(`useDownloadProgress: Failed to join session group ${sessionId}:`, err);
      });
    }

    // Register all event handlers
    signalRService.on('onProgressUpdate', handleProgressUpdate);
    signalRService.on('onSessionCompleted', handleSessionCompleted);
    signalRService.on('onSessionFailed', handleSessionFailed);
    signalRService.on('onItemCompleted', handleItemCompleted);
    signalRService.on('onItemFailed', handleItemFailed);

    return () => {
      // Leave session group if sessionId is provided
      if (sessionId) {
        console.log(`useDownloadProgress: Leaving session group ${sessionId}`);
        signalRService.leaveSessionGroup(sessionId).catch(err => {
          console.error(`useDownloadProgress: Failed to leave session group ${sessionId}:`, err);
        });
      }

      // Unregister all event handlers
      signalRService.off('onProgressUpdate');
      signalRService.off('onSessionCompleted');
      signalRService.off('onSessionFailed');
      signalRService.off('onItemCompleted');
      signalRService.off('onItemFailed');
    };
  }, [sessionId]);

  return {
    downloadProgress,
    downloadSpeeds,
    sessionData,
    progress,
    isConnected,
    connectionState
  };
}