import { useState, useEffect } from 'react';
import { Folder, File, Download, Calendar, HardDrive } from 'lucide-react';
import { DownloadFolder } from '@/types/api';
import { downloadApi } from '@/services/api';

interface DownloadHistoryPanelProps {
  onClearHistory?: () => void;
}

export const DownloadHistoryPanel = ({ onClearHistory }: DownloadHistoryPanelProps) => {
  const [folders, setFolders] = useState<DownloadFolder[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [expandedFolders, setExpandedFolders] = useState<Set<string>>(new Set());

  useEffect(() => {
    fetchDownloadFolders();
  }, []);

  const fetchDownloadFolders = async () => {
    try {
      setLoading(true);
      const data = await downloadApi.getFolders();
      setFolders(data);
      setError(null);
    } catch (err) {
      setError('Failed to load download folders');
      console.error('Error fetching download folders:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleClearHistory = async () => {
    if (window.confirm('Are you sure you want to clear all download history? This action cannot be undone.')) {
      if (onClearHistory) {
        onClearHistory();
      }
    }
  };

  const formatBytes = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const formatDate = (dateString: string): string => {
    return new Date(dateString).toLocaleString();
  };

  const toggleFolder = (folderName: string) => {
    const newExpanded = new Set(expandedFolders);
    if (newExpanded.has(folderName)) {
      newExpanded.delete(folderName);
    } else {
      newExpanded.add(folderName);
    }
    setExpandedFolders(newExpanded);
  };

  const getCompatibilityColor = (name: string) => {
    switch (name) {
      case '2020':
        return 'bg-blue-100 text-blue-800';
      case '2024':
        return 'bg-green-100 text-green-800';
      case '2020-2024':
        return 'bg-purple-100 text-purple-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-32">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-md p-4">
        <div className="flex">
          <div className="flex-shrink-0">
            <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
            </svg>
          </div>
          <div className="ml-3">
            <h3 className="text-sm font-medium text-red-800">{error}</h3>
            <div className="mt-2">
              <button
                onClick={fetchDownloadFolders}
                className="text-sm bg-red-100 text-red-800 px-3 py-1 rounded-md hover:bg-red-200"
              >
                Retry
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (folders.length === 0) {
    return (
      <div className="text-center py-8">
        <Download className="mx-auto h-12 w-12 text-gray-400" />
        <h3 className="mt-2 text-sm font-medium text-gray-900">No downloads found</h3>
        <p className="mt-1 text-sm text-gray-500">
          Downloaded files will appear here organized by compatibility.
        </p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h3 className="text-lg font-medium text-gray-900">Downloaded Files</h3>
        <div className="flex items-center space-x-2">
          <button
            onClick={fetchDownloadFolders}
            className="text-sm text-blue-600 hover:text-blue-800 px-3 py-1 rounded hover:bg-blue-50"
          >
            Refresh
          </button>
          <button
            onClick={handleClearHistory}
            className="text-sm text-red-600 hover:text-red-800 px-3 py-1 rounded hover:bg-red-50"
          >
            Clear History
          </button>
        </div>
      </div>

      {folders.map((folder) => (
        <div key={folder.name} className="bg-white border border-gray-200 rounded-lg shadow-sm">
          <div 
            className="p-4 cursor-pointer hover:bg-gray-50"
            onClick={() => toggleFolder(folder.name)}
          >
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-3">
                <Folder className="w-5 h-5 text-blue-500" />
                <div>
                  <div className="flex items-center space-x-2">
                    <h4 className="text-sm font-medium text-gray-900">{folder.name}</h4>
                    <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${getCompatibilityColor(folder.name)}`}>
                      {folder.name === '2020-2024' ? 'Universal' : folder.name}
                    </span>
                  </div>
                  <div className="flex items-center space-x-4 mt-1 text-sm text-gray-500">
                    <span className="flex items-center space-x-1">
                      <File className="w-4 h-4" />
                      <span>{folder.fileCount} files</span>
                    </span>
                    <span className="flex items-center space-x-1">
                      <HardDrive className="w-4 h-4" />
                      <span>{formatBytes(folder.totalSize)}</span>
                    </span>
                  </div>
                </div>
              </div>
              <div className="text-sm text-gray-400">
                {expandedFolders.has(folder.name) ? '−' : '+'}
              </div>
            </div>
          </div>

          {expandedFolders.has(folder.name) && (
            <div className="border-t border-gray-200 bg-gray-50">
              <div className="p-4">
                {folder.files.length === 0 ? (
                  <p className="text-sm text-gray-500 text-center py-4">No files in this folder</p>
                ) : (
                  <div className="space-y-2 max-h-64 overflow-y-auto">
                    {folder.files.map((file, index) => (
                      <div
                        key={index}
                        className="flex items-center justify-between p-3 bg-white rounded-md border border-gray-200 hover:bg-gray-50"
                      >
                        <div className="flex items-center space-x-3 flex-1 min-w-0">
                          <File className="w-4 h-4 text-gray-400 flex-shrink-0" />
                          <div className="flex-1 min-w-0">
                            <p className="text-sm font-medium text-gray-900 truncate" title={file.fileName}>
                              {file.fileName}
                            </p>
                            <div className="flex items-center space-x-3 mt-1">
                              <span className="text-xs text-gray-500">
                                {formatBytes(file.fileSize)}
                              </span>
                              <span className="flex items-center space-x-1 text-xs text-gray-500">
                                <Calendar className="w-3 h-3" />
                                <span>{formatDate(file.createdAt)}</span>
                              </span>
                            </div>
                          </div>
                        </div>
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            // Open file location or download again
                            console.log('File action:', file.filePath);
                          }}
                          className="text-xs text-blue-600 hover:text-blue-800 px-2 py-1 rounded hover:bg-blue-50"
                        >
                          Show in folder
                        </button>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          )}
        </div>
      ))}
    </div>
  );
};