import React from 'react';
import { useSignalRContext } from '@/contexts/SignalRContext';
import { Wifi, WifiOff, Loader2 } from 'lucide-react';

interface ConnectionStatusProps {
  className?: string;
  showText?: boolean;
  showIcon?: boolean;
  onlyShowWhenDisconnected?: boolean;
}

export const ConnectionStatus: React.FC<ConnectionStatusProps> = ({
  className = '',
  showText = true,
  showIcon = true,
  onlyShowWhenDisconnected = false
}) => {
  const { connectionState, isConnected } = useSignalRContext();
  
  // Don't show anything if we're connected and onlyShowWhenDisconnected is true
  if (isConnected && onlyShowWhenDisconnected) {
    return null;
  }

  let statusText = '';
  let statusColor = '';
  let icon = null;

  switch (connectionState) {
    case 'Connected':
      statusText = 'Connected';
      statusColor = 'text-green-600';
      icon = <Wifi className="w-4 h-4" />;
      break;
    case 'Connecting':
      statusText = 'Connecting...';
      statusColor = 'text-yellow-600';
      icon = <Loader2 className="w-4 h-4 animate-spin" />;
      break;
    case 'Reconnecting':
      statusText = 'Reconnecting...';
      statusColor = 'text-orange-600';
      icon = <Loader2 className="w-4 h-4 animate-spin" />;
      break;
    case 'Disconnected':
      statusText = 'Disconnected';
      statusColor = 'text-red-600';
      icon = <WifiOff className="w-4 h-4" />;
      break;
    default:
      statusText = 'Unknown';
      statusColor = 'text-gray-600';
      icon = <Wifi className="w-4 h-4" />;
  }

  return (
    <div className={`flex items-center space-x-1 ${statusColor} ${className}`}>
      {showIcon && icon}
      {showText && <span className="text-sm font-medium">{statusText}</span>}
    </div>
  );
};