import { useState } from 'react';
import { ChevronDown, ChevronUp, Play, Pause, X, Clock, CheckCircle, AlertCircle } from 'lucide-react';
import { DownloadStatusResponse, SessionStatus } from '@/types/api';
import { DownloadItemCard } from './DownloadItemCard';

interface DownloadSessionCardProps {
  session: DownloadStatusResponse;
  onPauseSession?: (sessionId: string) => void;
  onCancelSession?: (sessionId: string) => void;
  onResumeSession?: (sessionId: string) => void;
  onPauseItem?: (sessionId: string, itemId: string) => void;
  onResumeItem?: (sessionId: string, itemId: string) => void;
  onCancelItem?: (sessionId: string, itemId: string) => void;
  showControls?: boolean;
}

export const DownloadSessionCard = ({
  session,
  onPauseSession,
  onCancelSession,
  onResumeSession,
  onPauseItem,
  onResumeItem,
  onCancelItem,
  showControls = true
}: DownloadSessionCardProps) => {
  const [isExpanded, setIsExpanded] = useState(true);

  const formatBytes = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const formatSpeed = (bytesPerSecond: number): string => {
    return `${formatBytes(bytesPerSecond)}/s`;
  };

  const formatETA = (seconds: number): string => {
    if (seconds <= 0) return '';
    
    if (seconds < 60) {
      return `${Math.round(seconds)}s`;
    } else if (seconds < 3600) {
      return `${Math.round(seconds / 60)}m`;
    } else {
      const hours = Math.floor(seconds / 3600);
      const minutes = Math.round((seconds % 3600) / 60);
      return `${hours}h ${minutes}m`;
    }
  };

  const getSessionIcon = () => {
    switch (session.status) {
      case SessionStatus.Active:
        return <Play className="w-5 h-5 text-blue-500" />;
      case SessionStatus.Completed:
        return <CheckCircle className="w-5 h-5 text-green-500" />;
      case SessionStatus.CompletedWithErrors:
        return <AlertCircle className="w-5 h-5 text-yellow-500" />;
      case SessionStatus.Cancelled:
        return <X className="w-5 h-5 text-gray-500" />;
      default:
        return <Clock className="w-5 h-5 text-gray-400" />;
    }
  };

  const getSessionStatusColor = () => {
    switch (session.status) {
      case SessionStatus.Active:
        return 'bg-blue-100 text-blue-800';
      case SessionStatus.Completed:
        return 'bg-green-100 text-green-800';
      case SessionStatus.CompletedWithErrors:
        return 'bg-yellow-100 text-yellow-800';
      case SessionStatus.Cancelled:
        return 'bg-gray-100 text-gray-800';
      default:
        return 'bg-gray-100 text-gray-600';
    }
  };

  const isActive = session.status === SessionStatus.Active;
  const isCompleted = session.status === SessionStatus.Completed || session.status === SessionStatus.CompletedWithErrors;
  const isCancelled = session.status === SessionStatus.Cancelled;
  
  // Button visibility logic - show appropriate buttons for all sessions
  const canPause = isActive && showControls;
  const canResume = !isActive && showControls; // Allow resume for any non-active session
  const canCancel = !isCancelled && showControls; // Allow cancel for any non-cancelled session

  const totalBytes = session.items.reduce((sum, item) => sum + item.totalBytes, 0);
  const downloadedBytes = session.items.reduce((sum, item) => sum + item.downloadedBytes, 0);

  return (
    <div className={`bg-white border-2 rounded-lg shadow-sm transition-all duration-300 ${
      isActive
        ? 'border-blue-600 shadow-lg ring-2 ring-blue-400 ring-opacity-50'
        : 'border-gray-400 opacity-80'
    }`}>
      {/* Session Header */}
      <div className={`p-4 border-b ${
        isActive ? 'border-blue-300 bg-blue-100' : 'border-gray-300 bg-gray-100'
      }`}>
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            {getSessionIcon()}
            <div>
              <h3 className="text-lg font-medium text-gray-900">
                Session {session.sessionId.substring(0, 8)}
              </h3>
              <div className="flex items-center space-x-4 mt-1">
                <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${getSessionStatusColor()}`}>
                  {session.status}
                </span>
                <span className="text-sm text-gray-500">
                  {session.completedItems}/{session.totalItems} completed
                </span>
                {session.failedItems > 0 && (
                  <span className="text-sm text-red-600">
                    {session.failedItems} failed
                  </span>
                )}
                {isActive && session.totalSpeedBytesPerSecond > 0 && (
                  <span className="text-blue-600 font-semibold bg-blue-50 px-2 py-1 rounded text-sm">
                    {formatSpeed(session.totalSpeedBytesPerSecond)}
                  </span>
                )}
              </div>
            </div>
          </div>

          <div className="flex items-center space-x-2">
            {/* Session Controls */}
            {showControls && (
              <div className="flex items-center space-x-1">
                {canResume && onResumeSession && (
                  <button
                    onClick={() => onResumeSession(session.sessionId)}
                    className="p-2 text-green-600 hover:text-green-800 hover:bg-green-50 rounded-md"
                    title="Resume Session"
                  >
                    <Play className="w-4 h-4" />
                  </button>
                )}
                {canPause && onPauseSession && (
                  <button
                    onClick={() => onPauseSession(session.sessionId)}
                    className="p-2 text-yellow-600 hover:text-yellow-800 hover:bg-yellow-50 rounded-md"
                    title="Pause Session"
                  >
                    <Pause className="w-4 h-4" />
                  </button>
                )}
                {canCancel && onCancelSession && (
                  <button
                    onClick={() => onCancelSession(session.sessionId)}
                    className="p-2 text-red-600 hover:text-red-800 hover:bg-red-50 rounded-md"
                    title="Cancel Session"
                  >
                    <X className="w-4 h-4" />
                  </button>
                )}
              </div>
            )}

            {/* Expand/Collapse Button */}
            <button
              onClick={() => setIsExpanded(!isExpanded)}
              className="p-2 text-gray-400 hover:text-gray-600 hover:bg-gray-50 rounded-md"
              title={isExpanded ? 'Collapse' : 'Expand'}
            >
              {isExpanded ? <ChevronUp className="w-4 h-4" /> : <ChevronDown className="w-4 h-4" />}
            </button>
          </div>
        </div>

        {/* Session Progress */}
        <div className="mt-4 space-y-2">
          <div className="w-full bg-gray-200 rounded-full h-2 overflow-hidden">
            <div
              className={`h-2 rounded-full transition-all duration-300 ${
                isActive
                  ? 'bg-blue-500 animate-pulse'
                  : session.status === SessionStatus.Completed
                    ? 'bg-green-500'
                    : session.status === SessionStatus.CompletedWithErrors
                      ? 'bg-yellow-500'
                      : 'bg-gray-400'
              }`}
              style={{ width: `${Math.max(0, Math.min(100, session.overallProgress))}%` }}
            />
          </div>
          
          <div className="flex items-center justify-between text-sm text-gray-600">
            <div className="flex items-center space-x-4">
              <span className="font-medium">
                {session.overallProgress.toFixed(1)}%
              </span>
              <span>
                {formatBytes(downloadedBytes)} / {formatBytes(totalBytes)}
              </span>
              {isActive && session.totalSpeedBytesPerSecond > 0 && (
                <span className="text-blue-600 font-medium">
                  {formatSpeed(session.totalSpeedBytesPerSecond)}
                </span>
              )}
              {formatETA(session.estimatedTimeRemainingSeconds) && (
                <span className="text-green-600 font-medium bg-green-50 px-2 py-1 rounded text-sm">
                  ETA: {formatETA(session.estimatedTimeRemainingSeconds)}
                </span>
              )}
            </div>
            
            <div className="flex items-center space-x-2 text-xs text-gray-500">
              <span>
                Started: {new Date(session.startedAt).toLocaleString()}
              </span>
              {session.completedAt && (
                <span>
                  • Completed: {new Date(session.completedAt).toLocaleString()}
                </span>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Session Items */}
      {isExpanded && (
        <div className={`p-4 ${isActive ? 'bg-blue-50/30' : 'bg-gray-50/50'}`}>
          <div className="space-y-3">
            {session.items.map((item) => (
              <DownloadItemCard
                key={item.id}
                item={item}
                showControls={showControls}
                onPause={onPauseItem ? (itemId) => onPauseItem(session.sessionId, itemId) : undefined}
                onResume={onResumeItem ? (itemId) => onResumeItem(session.sessionId, itemId) : undefined}
                onCancel={onCancelItem ? (itemId) => onCancelItem(session.sessionId, itemId) : undefined}
              />
            ))}
          </div>
        </div>
      )}
    </div>
  );
};