import { Play, Pause, X, CheckCircle, AlertCircle, Clock, Download } from 'lucide-react';
import { DownloadItem, DownloadStatus } from '@/types/api';

interface DownloadItemCardProps {
  item: DownloadItem;
  onPause?: (itemId: string) => void;
  onCancel?: (itemId: string) => void;
  onResume?: (itemId: string) => void;
  showControls?: boolean;
}

export const DownloadItemCard = ({
  item,
  onPause,
  onCancel,
  onResume,
  showControls = true
}: DownloadItemCardProps) => {
  const formatBytes = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const formatSpeed = (bytesPerSecond: number): string => {
    return `${formatBytes(bytesPerSecond)}/s`;
  };

  const formatETA = (seconds: number): string => {
    if (seconds <= 0) return '';
    
    if (seconds < 60) {
      return `${Math.round(seconds)}s`;
    } else if (seconds < 3600) {
      return `${Math.round(seconds / 60)}m`;
    } else {
      const hours = Math.floor(seconds / 3600);
      const minutes = Math.round((seconds % 3600) / 60);
      return `${hours}h ${minutes}m`;
    }
  };

  const getStatusIcon = () => {
    switch (item.status) {
      case DownloadStatus.Completed:
        return <CheckCircle className="w-5 h-5 text-green-500" />;
      case DownloadStatus.Failed:
        return <AlertCircle className="w-5 h-5 text-red-500" />;
      case DownloadStatus.Cancelled:
        return <X className="w-5 h-5 text-gray-500" />;
      case DownloadStatus.Downloading:
        return <Download className="w-5 h-5 text-blue-500 animate-bounce" />;
      case DownloadStatus.Queued:
        return <Clock className="w-5 h-5 text-yellow-500" />;
      case DownloadStatus.Skipped:
        return <CheckCircle className="w-5 h-5 text-gray-400" />;
      default:
        return <Clock className="w-5 h-5 text-gray-400" />;
    }
  };

  const getStatusColor = () => {
    switch (item.status) {
      case DownloadStatus.Completed:
        return 'bg-green-100 text-green-800';
      case DownloadStatus.Failed:
        return 'bg-red-100 text-red-800';
      case DownloadStatus.Cancelled:
        return 'bg-gray-100 text-gray-800';
      case DownloadStatus.Downloading:
        return 'bg-blue-100 text-blue-800';
      case DownloadStatus.Queued:
        return 'bg-yellow-100 text-yellow-800';
      case DownloadStatus.Skipped:
        return 'bg-gray-100 text-gray-600';
      default:
        return 'bg-gray-100 text-gray-600';
    }
  };

  const getProgressBarColor = () => {
    switch (item.status) {
      case DownloadStatus.Completed:
        return 'bg-green-500';
      case DownloadStatus.Failed:
        return 'bg-red-500';
      case DownloadStatus.Cancelled:
        return 'bg-gray-400';
      case DownloadStatus.Downloading:
        return 'bg-blue-500';
      default:
        return 'bg-gray-300';
    }
  };

  const isActive = item.status === DownloadStatus.Downloading;
  const isPaused = item.status === DownloadStatus.Queued;
  const isCompleted = item.status === DownloadStatus.Completed || item.status === DownloadStatus.Skipped;
  const isFailed = item.status === DownloadStatus.Failed;
  const isCancelled = item.status === DownloadStatus.Cancelled;
  
  // Button visibility logic - show appropriate buttons for all items
  const canPause = isActive && showControls;
  const canResume = (!isActive || isPaused || isFailed) && showControls; // Allow resume for any non-active item
  const canCancel = !isCancelled && !isCompleted && showControls; // Allow cancel for any non-completed/cancelled item

  return (
    <div className={`border rounded-lg p-4 shadow-sm transition-all duration-300 ${
      isActive
        ? 'bg-white border-blue-300 shadow-md hover:shadow-lg ring-1 ring-blue-100'
        : item.status === DownloadStatus.Completed
          ? 'bg-green-50 border-green-200 hover:shadow-md'
          : item.status === DownloadStatus.Failed
            ? 'bg-red-50 border-red-200 hover:shadow-md'
            : 'bg-gray-50 border-gray-200 hover:shadow-md opacity-90'
    }`}>
      {/* Header */}
      <div className="flex items-start justify-between mb-3">
        <div className="flex items-start space-x-3 flex-1 min-w-0">
          {getStatusIcon()}
          <div className="flex-1 min-w-0">
            <h3 className="text-sm font-medium text-gray-900 truncate" title={item.name}>
              {item.name}
            </h3>
            <p className="text-xs text-gray-500 truncate" title={item.fileName}>
              {item.fileName}
            </p>
            <div className="flex items-center space-x-2 mt-1">
              <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${getStatusColor()}`}>
                {item.status}
              </span>
              <span className="text-xs text-gray-500">
                {item.compatibility}
              </span>
            </div>
          </div>
        </div>

        {/* Controls */}
        {showControls && (
          <div className="flex items-center space-x-1 ml-2">
            {canResume && onResume && (
              <button
                onClick={() => onResume(item.id)}
                className="p-1 text-green-600 hover:text-green-800 hover:bg-green-50 rounded"
                title="Resume Download"
              >
                <Play className="w-4 h-4" />
              </button>
            )}
            {canPause && onPause && (
              <button
                onClick={() => onPause(item.id)}
                className="p-1 text-yellow-600 hover:text-yellow-800 hover:bg-yellow-50 rounded"
                title="Pause Download"
              >
                <Pause className="w-4 h-4" />
              </button>
            )}
            {canCancel && onCancel && (
              <button
                onClick={() => onCancel(item.id)}
                className="p-1 text-red-600 hover:text-red-800 hover:bg-red-50 rounded"
                title="Cancel Download"
              >
                <X className="w-4 h-4" />
              </button>
            )}
          </div>
        )}
      </div>

      {/* Progress Section */}
      <div className="space-y-2">
        {/* Progress Bar */}
        <div className="w-full bg-gray-200 rounded-full h-2 overflow-hidden">
          <div
            className={`h-2 rounded-full transition-all duration-300 ${getProgressBarColor()} ${
              isActive ? 'animate-pulse' : ''
            }`}
            style={{ width: `${Math.max(0, Math.min(100, item.progress))}%` }}
          />
        </div>

        {/* Progress Details */}
        <div className="flex items-center justify-between text-xs text-gray-600">
          <div className="flex items-center space-x-3">
            <span className="font-medium">
              {item.progress.toFixed(1)}%
            </span>
            <span>
              {formatBytes(item.downloadedBytes)} / {formatBytes(item.totalBytes)}
            </span>
          </div>
          
          {/* Speed and Status */}
          <div className="flex items-center space-x-2">
            {item.speedBytesPerSecond > 0 && (
              <span className={`font-medium px-2 py-1 rounded ${
                isActive
                  ? 'text-blue-600 bg-blue-100 animate-pulse'
                  : 'text-blue-600 bg-blue-50'
              }`}>
                {formatSpeed(item.speedBytesPerSecond)}
              </span>
            )}
            {formatETA(item.estimatedTimeRemainingSeconds) && (
              <span className="text-green-600 font-medium bg-green-50 px-2 py-1 rounded text-xs">
                ETA: {formatETA(item.estimatedTimeRemainingSeconds)}
              </span>
            )}
            {item.status === DownloadStatus.Failed && item.errorMessage && (
              <span className="text-red-600 text-xs truncate max-w-xs" title={item.errorMessage}>
                Error: {item.errorMessage}
              </span>
            )}
          </div>
        </div>

        {/* Timestamps */}
        {(item.startedAt || item.completedAt) && (
          <div className="flex items-center justify-between text-xs text-gray-500">
            {item.startedAt && (
              <span>
                Started: {new Date(item.startedAt).toLocaleTimeString()}
              </span>
            )}
            {item.completedAt && (
              <span>
                Completed: {new Date(item.completedAt).toLocaleTimeString()}
              </span>
            )}
          </div>
        )}
      </div>
    </div>
  );
};