import React from 'react';
import { useSignalRContext } from '@/contexts/SignalRContext';
import { RefreshCw } from 'lucide-react';

export default function ScraperStatusIndicator() {
  const { scraperStatus } = useSignalRContext();

  if (!scraperStatus?.isActive) {
    return null;
  }

  return (
    <div className="fixed bottom-4 right-4 bg-blue-600 text-white rounded-lg shadow-lg p-4 flex items-center space-x-3 z-50">
      <RefreshCw className="w-5 h-5 animate-spin" />
      <div>
        <div className="font-semibold">Scraper Running</div>
        <div className="text-sm opacity-90">{scraperStatus.progress}</div>
      </div>
    </div>
  );
}